.tarea-contenedor {
    width: 500px;
    min-height: 65px;
    background-color: #28536B;
    margin: 5px 0;
    padding: 8px 15px 8px 20px;
    display: flex;
    justify-content: space-between;
    align-items: center;
    border-radius: 7px;
    border: 1px solid white;
    cursor: pointer;
}

.tarea-texto {
    width: 100%;
    height: 100%;
    font-size: 20px;
    display: flex;
    align-items: center;
    overflow-wrap: anywhere;
}

.tarea-icono {
    width: 25px;
    height: 25px;
    margin: 5px;
}

/* Tarea Completada*/

.tarea-contenedor.completada{
    background-color: #922D50;
    text-decoration: line-through;
}